.time_wrapper {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.box_wrapper {
  width: 100%;
  max-width: 110px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin-right: 30px;
}

.box_wrapper:last-of-type {
  margin-right: 0;
}

.time_box {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background: #ffffff;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 4px;
}

.name {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 1.33333333333;
  color: #000000;
  margin-top: 12px;
}

.name.dark {
  color: #ffffff;
}

.input {
  width: 100%;
  height: 100%;
  text-align: center;
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 45px;
  line-height: 1.31111111111;
  color: #000000;
  background: #ffffff;
  border: 1px solid #dbdbdb;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 0;
}

.input::-webkit-input-placeholder {
  color: #000000;
}

.input::-moz-placeholder {
  color: #000000;
}

.input:-ms-input-placeholder {
  color: #000000;
}

.input::-ms-input-placeholder {
  color: #000000;
}

.input::placeholder {
  color: #000000;
}

.input.dark {
  background: #011c2c;
  border: 1px solid #dbdbdb;
  color: #ffffff;
}

.input.dark::-webkit-input-placeholder {
  color: #ffffff;
}

.input.dark::-moz-placeholder {
  color: #ffffff;
}

.input.dark:-ms-input-placeholder {
  color: #ffffff;
}

.input.dark::-ms-input-placeholder {
  color: #ffffff;
}

.input.dark::placeholder {
  color: #ffffff;
}

.input:disabled {
  width: 100%;
  height: 100%;
  text-align: center;
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 45px;
  line-height: 1.31111111111;
  color: #000000;
  background: #ffffff;
  border: 1px solid #dbdbdb;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 0;
}

.input.dark:disabled {
  width: 100%;
  height: 100%;
  text-align: center;
  font-family: Roboto Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 45px;
  line-height: 1.31111111111;
  background: #011c2c;
  color: #ffffff;
  border: 1px solid #dbdbdb;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

@media all and (max-width: 465px) {
  .box_wrapper {
    margin-right: 15px;
  }
}
